import { Timespan, toJson } from './Common';
import { IClientDescription, IRegistrarClient, IRegistrarClientOptions, ITelemetrySender,
         ITransports, LogProvider, SkypeTokenProvider, TelemetryEvent } from './Interfaces';


/**
 * Thrown when retry process was cancelled
 */
class CancelationError extends Error {
    constructor(message: string) {
        super(message);
        this.name = 'CancelationError';
    }
}

/**
 * Randomized exponential delay used for retries
 */
class ExponentialDelay {
    private static idCounter: number = 0;

    public readonly id: number;

    private backoffCount: number = 0;
    private timerHandle?: number;
    // tslint:disable-next-line: no-any
    private cancelFunc: ((err?: any) => void) | undefined;

    public constructor(
        private logger: LogProvider,
        private maxBackoffInMs: number,
        private initialDelay: number
    ) {
        this.id = ++ExponentialDelay.idCounter;
    }

    // tslint:disable-next-line: promise-function-async
    public delay(taskName: string): Promise<void> {
        if (this.timerHandle !== undefined) {
            throw new Error('Retry sequence logical failure');
        }

        if (this.backoffCount === -1) {
            return new Promise((resolve, reject) => { reject(new CancelationError('Cancelled')); });
        }

        const backoffMs = this.calculateNextBackoffMs();
        this.backoffCount++;
        this.logger.info(`[RegistrarClient] Backing off ${taskName} for ${backoffMs} milliseconds with ID ${this.id}`);

        return new Promise( (resolve, reject) => {
            this.cancelFunc = reject;
            this.timerHandle = setTimeout(() => {
                this.logger.info(`[RegistrarClient] Back off for ${taskName} with ID ${this.id} complete`);
                this.timerHandle = undefined;
                resolve();
            }, backoffMs);
        });
    }

    public cancel(): void {
        if (this.timerHandle !== undefined) {
            this.logger.debug('Resetting back off');
            clearTimeout(this.timerHandle);
            if (this.cancelFunc !== undefined) {
                this.cancelFunc(new CancelationError('Cancelled'));
            }
        }
        this.backoffCount = -1;
    }

    private calculateNextBackoffMs(): number {
        // tslint:disable-next-line:insecure-random
        const fudge = 1 + (Math.random() - 0.5) * 0.4; // +/- 20%
        let backoff = this.initialDelay * Math.pow(2, this.backoffCount) * fudge;
        backoff = Math.round(backoff);

        return Math.min(this.maxBackoffInMs, backoff);
    }
}

/**
 * Registrar client implementation
 */
export class RegistrarClient implements IRegistrarClient {
    private backoffs: { [id: string]: ExponentialDelay };
    private eventLogger?: ITelemetrySender;
    private cachedRegistrationParams?: [IClientDescription, ITransports];

    constructor(
        private logger: LogProvider,
        private skypeTokenProvider: SkypeTokenProvider,
        private options: IRegistrarClientOptions
    ) {
        this.backoffs = {};
    }

    public setTelemetryLogger(eventLogger: ITelemetrySender) {
        this.eventLogger = eventLogger;
    }

    public async register(client: IClientDescription, transports: ITransports): Promise<void> {
        await this.performRegistration(client, transports, 'pr_set_registration');
        this.cachedRegistrationParams = [client, transports];
    }

    public async unregister(): Promise<void> {
        this.logger.info('[RegistrarClient] sending unregister request');

        const unregisterRequest = new Request(`${this.options.registrarUrl}/${this.options.registrationId}`,
            {
                method: 'DELETE',
                mode: 'cors',
                headers: new Headers({
                    accept: 'application/json, text/javascript'
                })
            });

            await this.callRegistrar(unregisterRequest, 'pr_delete_registration');
    }

    public cancelPendingRequests() {
        Object.keys(this.backoffs).forEach((id: string) => {
            this.backoffs[id].cancel();
        });
        this.backoffs = {};
    }

    public async resendRegistration(): Promise<void> {
        if (!this.cachedRegistrationParams) {
            throw new Error('Re-registration failed because there is no registration parameters cached');
        }

        await this.performRegistration(this.cachedRegistrationParams[0], this.cachedRegistrationParams[1], 'pr_resend_registration');
    }

    private async performRegistration(client: IClientDescription, transports: ITransports, eventName: string) {
        this.logger.info('[RegistrarClient] Sending register request');
        const payload = {
            clientDescription: client,
            registrationId: this.options.registrationId,
            nodeId: '',
            transports: transports
        };
        const registerRequest = new Request(this.options.registrarUrl, {
            method: 'POST',
            mode: 'cors',
            headers: new Headers({
                'content-type': 'application/json',
                accept: 'application/json, text/javascript'
            }),
            body: toJson(payload)
        });
        await this.callRegistrar(registerRequest, eventName);
    }

    private startBackoff() {
        const backoff = new ExponentialDelay(this.logger, this.options.maxRetryDelayMs, this.options.initialRetryDelayMs);
        this.backoffs[backoff.id] = backoff;

        return backoff;
    }

    private stopBackoff(backoff: ExponentialDelay) {
        backoff.cancel();
        delete this.backoffs[backoff.id];
    }

    private async getSkypeToken(): Promise<string> {
        const backoff = this.startBackoff();
        for (;;) {
            try {
                this.logger.info('[RegistrarClient] Asking for a new skypetoken');
                const token = await this.skypeTokenProvider(true);
                this.stopBackoff(backoff);

                return token;
            } catch (error) {
                try {
                    await backoff.delay('Fetching a new skypetoken');
                    continue;
                } catch (cancelError) {
                    this.stopBackoff(backoff);
                    throw cancelError;
                }
            }
        }
    }

    private async callRegistrar(request: Request, eventName: string): Promise<Response> {
        const backoff = this.startBackoff();

        const cachedSkypetoken = await this.skypeTokenProvider(false);
        this.setSkypeTokenHeader(request, cachedSkypetoken);

        const stopwatch = new Timespan();

        for (;;) {
            let response: Response | undefined;
            try {
                // Make a clone in case we need to retry, one Request cannot be used twice with fetch()
                const req = request.clone();
                response = await this.fetchWithTimeout(req);
                if (response.status === 401) {
                    this.setSkypeTokenHeader(request, await this.getSkypeToken());
                    //should we wait before retrying with a new skypetoken?
                    continue;
                } else if (response.status >= 500 && response.status < 600) {
                    // throw and retry after in the catch section
                    throw new Error(`Fetch for '${request.url}' failed with ${response.status} ${response.statusText}`);
                }
            } catch (error) {
                this.logger.error(`[RegistrarClient] Request failed with ${error}`);
                try {
                    // retry on network errors (thrown by fetch) and retryable codes
                    await backoff.delay('Registrar call retry');
                    continue;
                } catch (canceledError) {
                    this.logger.error('[RegistrarClient] Request cancelled');
                    this.stopBackoff(backoff);
                    throw canceledError;
                }
            } finally {
                this.sendTelemetryEvent(eventName, request, response, stopwatch);
            }

            this.stopBackoff(backoff);
            if (response.ok) {
                return response;
            } else  {
                let details: string;
                try {
                    details = JSON.stringify(await response.json());
                } catch (error) {
                    details = 'no details';
                }
                // Note MS-CV will be null until Registrar service CORS fix
                // https://dev.azure.com/skype/SCC/_workitems/edit/2353812
                const message = `Fetch for '${request.url}' failed with ${response.status} ${response.statusText} (${details}, MS-CV: ${response.headers.get('MS-CV')})`;
                this.logger.error(`[RegistrarClient] ${message}`);
                throw new Error(message);
            }
        }
    }

    private setSkypeTokenHeader(request: Request, skypetoken: string) {
        // tslint:disable-next-line: no-backbone-get-set-outside-model
        request.headers.set('X-Skypetoken', skypetoken);
    }

    /** Wrapper to send request with specified timeout */
    // tslint:disable-next-line:promise-function-async
    private fetchWithTimeout(request: Request): Promise<Response> {
        return new Promise((resolve, reject) => {
            fetch(request).then(resolve).catch(reject);

            if (this.options.requestTimeoutMs !== 0) {
                // tslint:disable-next-line:no-string-based-set-timeout - false positive
                setTimeout(reject, this.options.requestTimeoutMs, new Error(`Fetch for '${request.url}' timed out`));
            }
        });
    }

    private sendTelemetryEvent(eventName: string, request: Request, response: Response | undefined, stopwatch: Timespan) {
        if (this.eventLogger === undefined) {
            return;
        }

        const clientEvent: TelemetryEvent = {
            name: eventName,
            properties: {
                url: { value: request.url },
                result_code: { value: response !== undefined ? response.status : 0 },
                begin_timestamp: { value: stopwatch.startTime },
                elapsed: { value: stopwatch.duration }
            }
        };

        this.eventLogger.logEvent(clientEvent);
    }
}

export function createRegistrarClient(
    logProvider: LogProvider,
    skypeTokenProvider: SkypeTokenProvider,
    options: IRegistrarClientOptions
): IRegistrarClient {
    return new RegistrarClient(logProvider, skypeTokenProvider, options);
}



